import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { useState, useEffect } from 'react';
import logo from "../assets/img/logo.png";


export const NavBar = () => {
    const [activeLink, setActiveLink] = useState("home");
    const [scrolled, seScrolled] = useState(false);

    useEffect(() => {
      const onScroll = () => {
        if (window.scrollY > 50) {
          seScrolled(true);
        }else{
          seScrolled(false);
        }
      }
      window.addEventListener("scroll", onscroll);
      return () => window.removeEventListener("scroll", onScroll);
    }, [])
    const onUpdateActiveLink = (value) => {
      setActiveLink(value);
    }
    
    return (
        <Navbar  expand="lg" className={scrolled ? "scrolled": ""}>
            <Container>
                < Navbar.Brand href="#home">
                <img src={logo} alt="Logo"/>
                </Navbar.Brand>
                  <Navbar.Toggle aria-controls="basic-navbar-nav"> 
                  <span className='navbar-toggler-icon'></span>
                  </Navbar.Toggle>
                  <Navbar.Collapse id="basic-navbar-nav">
                  <Nav className="me-auto">
                  <Nav.Link href="#home" className={activeLink  === "home" ? "active navbar-link" : "navbar-link"} onClick={() => onUpdateActiveLink("home")}>Home</Nav.Link>
                  <Nav.Link href="#skills" className={activeLink  === "skills" ? "active navbar-link" : "navbar-link"} onClick={() => onUpdateActiveLink("skills")}>Tecnologas</Nav.Link>
                  <Nav.Link href="#projects" className={activeLink  === "projects" ? "active navbar-link" : "navbar-link"} onClick={() => onUpdateActiveLink("about")}>Certificados</Nav.Link>
                  <Nav.Link href="#connect" className={activeLink  === "projects" ? "active navbar-link" : "navbar-link"} onClick={() => onUpdateActiveLink("connect")}>Ponte en contacto</Nav.Link>
                </Nav>
                <span className='navbar-text'>
                  <div className='social-icon'>

                  </div>
                </span>
              </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}