import React from "react";

function SaludoEmergente() {
  function mostrarSaludo() {
    let resultado = prompt("Ingresa tu nombre:", "");
    if (resultado == null || resultado == "") {
      console.log("Has cancelado o introducido el nombre vaco");
    } else {
      alert("Hola, " + resultado);
      console.log("Hola, " + resultado);
    }
  }

  return (
    <div>
      <button id="boton-saludo" onClick={mostrarSaludo}>
        Saludar
      </button>
    </div>
  );
}

export default SaludoEmergente;
